.fl-annotations .fl-annotations-cursor {
  visibility: hidden;
  pointer-events: none;
}
.fl-annotations.is-edit-add .fl-annotations-cursor {
  visibility: visible;
}
.fl-annotations.is-hiding .fl-annotations-cursor {
  visibility: hidden;
}
.fl-annotations .fl-annotations-cursor circle {
  fill: none;
  stroke: #eee;
  stroke-width: 2px;
  stroke-dasharray: 4,4;
  transition: stroke 0.25s;
}
.fl-annotations .fl-annotations-cursor.is-valid circle {
  stroke: #333333;
  animation: 5s linear 0s infinite normal cursorRotate;
}
.fl-annotations .fl-annotations-cursor line {
  stroke: #eee;
  stroke-width: 2px;
  transition: stroke 0.25s;
}
.fl-annotations .fl-annotations-cursor.is-valid line {
  stroke: #333333;
}
.fl-annotations .fl-annotations-cursor.inner-outline circle,
.fl-annotations .fl-annotations-cursor.is-valid.inner-outline circle {
  stroke-dasharray: 6,2;
  stroke-dashoffset: 1;
}
.fl-annotations .fl-annotations-cursor.inner-outline circle,
.fl-annotations .fl-annotations-cursor.is-valid.inner-outline circle,
.fl-annotations .fl-annotations-cursor.inner-outline line,
.fl-annotations .fl-annotations-cursor.is-valid.inner-outline line {
  stroke: #fff;
  stroke-width: 4px;
}

@keyframes cursorRotate {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
