.fl-annotations .fl-annotations-node {
  pointer-events: none;
}

.fl-annotations .fl-annotations-node.fl-connector-popup {
  pointer-events: all;
}

.fl-annotations.is-editing .fl-annotations-node {
  pointer-events: all;
}

.fl-annotations .fl-annotations-node.fl-point text,
.fl-annotations .fl-annotations-node.fl-marker text {
  fill: #fff;
  text-anchor: middle;
  dominant-baseline: middle;
  alignment-baseline: middle;
  font-size: 0.8rem;
  font-weight: 800;
}

.fl-annotations .fl-annotations-node.fl-marker.selected circle {
  fill: #333333;
}

.fl-annotations .fl-notch {
  fill: none;
  stroke-dasharray: 4,4;
  pointer-events: none;
}

.fl-annotations.is-editing .fl-notch {
  pointer-events: all;
}

.fl-annotations.is-editing.is-dragging .fl-notch {
  pointer-events: none;
}

.fl-annotations .fl-annotations-node .fl-connector,
.fl-annotations .fl-annotations-node text {
  pointer-events: none;
}
.fl-annotations.is-editing .fl-annotations-inline {
  pointer-events: all;
}

.fl-annotations .fl-annotations-inline a {
  pointer-events: all;
}

.fl-annotations.is-hiding .fl-annotations-inline,
.fl-annotations.is-hiding .fl-connector,
.fl-annotations.is-hiding .fl-annotations-node,
.fl-annotations.is-hiding .fl-annotations-node .main-circle {
  pointer-events: none;
  visibility: hidden;
}
