@import "./general.css";
@import "./project-editor.scss";
@import "./source-element.css";
@import "./annotation-nodes.css";
@import "./annotation-cursor.css";
@import "./annotation-list.css";
@import "./settings-tooltip.css";

.fl-disable-selection *,
.fl-disable-selection {
  user-select: none!important;
}

.fl-project-editor {
  z-index: 100000;
  overflow-y: auto;
  width: 18rem;
  background:#ffffff;
  box-shadow: 0 -2px 5px 0 rgba(0,0,0,0.2);
  height: 100vh;
  overflow: auto;
}

.fl-project-editor .side-panel .side-panel-inner {
  margin: 0;
  max-width: none;
}

.fl-project-editor .side-panel {
  padding: 0;
  background: transparent;
  height: auto;
  position: initial;
  overflow: none;
}

.fl-annotation-settings {
  border-top: 1px solid #eee;
}

.fl-annotation-contents :first-child {
  margin-top: 0;
}
.fl-annotation-contents :last-child {
  margin-bottom: 0;
}
.fl-annotation-contents a {
  color: inherit;
  -webkit-text-stroke-color: inherit;
}
.fl-annotation-contents img {
  -webkit-user-drag: none;
  -khtml-user-drag: none;
  -moz-user-drag: none;
  -o-user-drag: none;
  user-drag: none;
  pointer-events: none;
}

.empty-annotation-message {
  padding: 1rem;
  font-size: 1rem;
  text-align: center;
  color: #aaa;
}

.fl-project-editor .fl-annotation-settings {
  .toplevel-settings-block, .settings-block {
    display: none;
  }
}

.fl-project-editor.active-annotation .fl-annotation-settings {
  .toplevel-settings-block, .settings-block {
    display: block;
  }
  .empty-annotation-message {
    display: none;
  }
}

@media screen and (min-width: 521px) {
  .fl-annotations-add-element {
    display: none;
  }
}

@media screen and (max-width: 520px) {
  .fl-project-editor {
    right: 0;
    position: absolute;
  }

  .is-edit-settings.is-editing .fl-annotations-source-element,
  .is-edit-settings .fl-annotations-add-element {
    left: -21rem;
  }
}
