/* _theme.scss */
.fl-project-editor {
  font-family: "Source Sans Pro", sans-serif;
  color: #333;
  margin: 0;
  font-size: 16px;
  height: 100%;
  -webkit-font-smoothing: antialiased;

  * {
    box-sizing: border-box;
  }

  .no-select {
    user-select: none;
  }

  p, h1, h2, h3, h4 {
    margin: 0;
    font-weight: normal;
  }

  input, select, textarea {
    font-family: "Source Sans Pro", sans-serif;
  }

  a:link, a:visited {
    color: inherit;
    text-decoration: none;
  }
  a:hover, a:active {
    opacity: 0.75;
  }
}

/* _project-editor.scss */
.fl-project-editor {
  header {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
  }
  main {
    display: block;
    position: absolute;
    top: 80px;
    left: 0;
    width: 100%;
    height: calc(100vh - 80px);
    overflow-y: auto;
    transition: height 0.5s ease, top 0.5s ease;
    @media(min-width: 420px) {
      top: 92px;
      height: calc(100vh - 92px);
    }
  }
  .hide-editor-bar main {
    top: 50px;
    height: calc(100vh - 50px);
    @media(min-width: 420px) {
      top: 62px;
      height: calc(100vh - 62px);
    }
  }
  .user-nav {
    float: right;
    padding-top: 0;
    display: none;
    @media(min-width: 820px) {
      display: block;
      margin-top: 13px;
      height: 36px;
      .dropdown-head {
        #current-picture {
          height: 36px;
          width: 36px;
          border-radius: 9999px;
          margin-left: 10px;
        }
      }
      .dropdown-list {
        left: auto;
        right: -5px;
        margin-top: -5px;
      }
    }
  }

  .visualisation-editor {
    overflow-x: hidden;

    .template-chooser.visible + .tab-panes {
      display: none;
    }
    .tab-panes {
      height: 100%;
      width: 100%;
      .tab-pane, .row.editor, .row-inner, #visualisation {
        height: 100%;
        .editor-core {
          position: relative;
          @media (min-width: 820px) {
            min-height: 100%;
            padding-bottom: 40px;
          }
        }
      }
      .tab-pane {
        .side-panel {
          display: none;
        }
        &.active {
          .side-panel {
            display: block;
          }
        }
        &:not(.active) {
          #spreadsheet-container {
            input {
              display: none; /* This is to make sure that you can’t tab into the spreadsheet from preview*/
            }
          }
        }
      }
    }
  }

  .story-editor, .visualisation-editor {
    background: #f9f9f9;
  }

  .row.editor {
    padding: 0;
    .no-template & {
      display: none; /* In the viz editor, if no template selected, hide whole row*/
    }
    .row-inner {
      max-width: none;
      padding: 0 10px;
    }

    #visibility-status .label, .blueprint-tag {
      display: inline-block; /* Override default behaviour of hiding labels on smaller screen*/
      margin-left: 2px;
    }

    #visualisation,
    #story {
      height: 100%;
      width: 100%;
      transition: height 0.5s ease;
      position: relative;
      .editor-core {
        width: 100%;
        height: auto; /*100%;*/
        overflow: visible;
        transition: width 0.5s ease;
        position: relative;
        text-align: center;
        .preview-holder {
          width: 100%;
          height: 100%;
          padding-right: 4px;
          min-width: 100px;
          min-height: 100px;
          transition: margin-left 0.5s ease, width 0.5s ease, opacity 1s;
          position: relative;
          background: #fff;
          display: inline-block;
          box-shadow: 0 0 2px rgba(0, 0, 0, 0.2);
          #preview {
            & {
              text-align: right;
              border: none;
              background: white;
              display: block;
              width: 100%;
              height: 100%;
            }

            iframe {
              width: 100vw;
              border: none;
              position: relative;
            }

            height: 100%;
            max-height: 100%;
            overflow-y: auto;
            min-width: 100px; /* To match .preview-holder rule*/
            #blank-slide, /* The rest of the styles for this div are in _story_editor.scsss*/
            iframe {
              height: 100%;
              width: 100%;
              min-width: 100px; /* To match .preview-holder rule*/
              border: none;
              flex: 1; /* In story editor*/
            }
          }
          .loading-spinner {
            position: absolute;
            left: 50%;
            top: 50%;
            width: 27px;
            height: 27px;
            opacity: 0;
            pointer-events: none;
            z-index: 5;
            margin-top: -13.5px;
            margin-left: -13.5px;
          }
          .unsupported-notice {
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background-color: #f9f9f9;
            padding: 20px;
            z-index: 10;
          }
        }
        #resize-overlay {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          display: none;
          &.dragging {
            display: block;
          }
        }
        #resize-handle-container {
          position: absolute;
          width: 4px;
          bottom: 0;
          top: 0;
          right: 0;
          margin-bottom: 0;
          display: none;
          z-index: 100;
          transition: width 200ms;
          #resize-handle {
            cursor: ew-resize;
            position: absolute;
            width: 4px;
            bottom: 0;
            top: 0;
            right: 0;
            background: #eee;
            transition: width 100ms, background 100ms;
            &:after {
              display: block;
              content: '';
              position: absolute;
              top: 0;
              bottom: 0;
              left: -4px;
              background: transparent;
              right: 0;
              width: auto;
            }
          }
          .resize-handle-icon {
            position: absolute;
            right: 3px;
            top: 50%;
            margin-top: -15px;
            opacity: 0.8;
            pointer-events: none;
            transition: right 100ms;
            display: none;
          }
          & > i {
            position: absolute;
            top: 5px;
            left: 5px;
          }
          &:hover {
            width: 6px;
            #resize-handle {
              width: 6px;
              background: #ddd;
              &:after {
                left: -10px;
              }
            }
            .resize-handle-icon {
              right: 2px;
              display: block;
            }
          }
          &.dragging {
            #resize-handle:after {
              left: -200px;
              right: 0;
            }
          }
        }
        @media(min-width: 770px) {
          padding: 0;
          #resize-handle-container {
            display: block;
          }
        }
        @media(min-width: 820px) {
          padding: 0 10px 0 0;
        }
      }
    }
    #visualisation {
      padding: 10px 0 0;
    }
    #visualisation .editor-core .preview-holder iframe#preview {
      transform-origin: left top;
      &.mini-preview {
        z-index: 1000;
        opacity: 1;
        box-shadow: none;
        animation: fadeIn 500ms;
        overflow: hidden;
        pointer-events: none;

        @keyframes fadeIn {
          0% { opacity: 0; }
          1% { opacity: 0; }
          100% { opacity: 1; }
        }
      }
      @media(min-width: 820px) {
        width: 100%;
      }
    }
    #story .editor-core {
      .preview-holder {
        #preview iframe {
          height: calc(100% - 64px);
        }
        #preview.nav-style-none iframe {
          height: 100%;
        }
      }
      @media(min-width: 820px) {
        max-width: calc(100% - 220px);
        margin-left: 220px;
        min-height: calc(100vh - 94px);
      }
    }
    &.mobile {
      #visualisation,
      #story {
        .editor-core .preview-holder {
          width: 320px;
          iframe {
            width: 320px;
          }
        }
      }
    }
    &.mobile.landscape {
      #visualisation,
      #story {
        .editor-core .preview-holder {
          width: 500px;
          iframe {
            width: 500px;
          }
        }
      }
    }
    &.tablet {
      #visualisation,
      #story {
        .editor-core .preview-holder {
          width: 768px;
          iframe {
            width: 768px;
          }
        }
      }
    }
    &.tablet.landscape {
      #visualisation,
      #story {
        .editor-core .preview-holder {
          width: 1024px;
          iframe {
            width: 1024px;
          }
        }
      }
    }
    #visualisation .editor-core {
      @media(min-width: 820px) {
        max-width: calc(100% - 350px);
      }
    }
  }

  .project-header {
    padding: 0 10px;
    position: absolute;
    top: 2px;
    left: 48px;
    right: 0;
    width: auto;
    @media(min-width: 420px) {
      left: 60px;
      &:after {
        clear: both;
        display: block;
        content: '';
      }
    }
    @media(min-width: 620px) {
      height: 60px;
      z-index: 110;
    }
    #visibility-status, .read-only, .blueprint-tag {
      text-align: left;
      margin: -1px 0 0 3px;
      height: auto;
      line-height: 1em;
      display: inline-block;
      border-radius: 3px;
      vertical-align: top;
      @media(min-width: 420px) {
        font-size: 11px;
        padding: 2px 3px 1px;
      }
    }
    #visibility-status {
      cursor: pointer;
      &:hover {
        color: #333;
      }
    }
    #visibility-status, .read-only {
      color: #aaa;
      font-weight: normal;
      padding: 1px 3px 2px;
      background: #eee;
      &.public {
        background: #ffdc98;
        color: #000;
        &:hover {
          background: #EEC26D;
          color: #000;
        }
      }
    }
    .blueprint-tag {
      font-size: 9px;
      font-weight: bold;
      background: #2886B2;
      color: #fff;
      text-transform: uppercase;
      padding: 1px 5px 2px;
      cursor: default;
    }
    .project-settings {
      margin-left: 10px;
      font-size: 14px;
      font-weight: normal;
      line-height: 0;
      vertical-align: top;
      margin-bottom: -3px;
      display: inline-block;
      margin-top: -2px;
      z-index: 110;
      height: auto;
      .dropdown-head {
        padding-right: 5px;
        padding-left: 5px;
        padding-bottom: 10px;
        font-size: 16px;
      }
      .dropdown-list {
        margin-right: 0;
      }
      @media(min-width: 420px) {
        top: 0;
        margin-top: -4px;
        padding-top: 19px;
      }
      @media(min-width: 620px) {
        padding-top: 18px;
      }
    }
    .has-name + .project-settings {
      margin-left: 0;
    }
    .name {
      position: relative;
      vertical-align: top;
      font-weight: normal;
      font-style: italic;
      height: 23px;
      margin-left: 0;
      width: auto;
      display: inline-block;
      font-size: 16px;
      z-index: 110;
      margin-top: 3px;
      @media(min-width: 420px) {
        margin-top: 5px;
        height: 26px;
      }
      @media(min-width: 620px) {
        font-size: 18px;
      }
      @media(min-width: 820px) {
        font-size: 18px;
      }
      input {
        font-size: 1em;
        font-size: inherit;
        border: none;
        border-bottom: 1px solid #ccc;
        border-radius: 0;
        background: #fff;
        font-style: italic;
        padding: 0;
        display: inline-block;
        color: #aaa;
        position: absolute;
        left: 0;
        top: 0;
        box-sizing: content-box;
        outline: none;
        height: 23px;
        @media(min-width: 420px) {
          height: 26px;
        }
      }
      .name-width-setter {
        font-size: inherit;
        padding: 0;
        display: inline-block;
        opacity: 0;
        pointer-events: none;
        max-width: 140px;
        min-width: 40px;
        white-space: pre;
        @media(min-width: 420px) {
          max-width: 240px;
        }
        @media(min-width: 620px) {
          max-width: 340px;
        }
        @media(min-width: 820px) {
          max-width: 340px;
        }
      }
      &.not-editable {
        input {
          border-color: transparent;
          color: #333;
          top: 0;
          background: transparent;
          font-style: normal;
        }
      }
      &.has-name {
        input, .name-width-setter {
          font-style: normal;
          background: none;
        }
        input {
          color: #333;
          border-color: #ccc;
          top: 0;
          font-style: normal;
        }
        .name-overlay {
          display: block;
          top: 0;
        }
      }
      .name-overlay {
        display: none;
        content: '';
        position: absolute;
        left: 125px;
        top: 0;
        width: 20px;
        height: 100%;
        background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 1) 80%);
        @media(min-width: 420px) {
          left: 225px;
        }
        @media(min-width: 620px) {
          left: 325px;
        }
        @media(min-width: 820px) {
          left: 325px;
        }
      }
    }
    .project-author {
      color: #aaa;
      font-weight: normal;
      top: 30px;
      font-size: 11px;
      line-height: 1em;
      position: absolute;
      @media(min-width: 420px) {
        top: 38px;
        font-size: 12px;
      }
      @media(min-width: 820px) {
        font-size: 12px;
      }
      + .dropdown {
        .dropdown-head {
          display: inline-block;
        }
        .badge {
          top: -14px;
        }
        .dropdown-list .badge {
          position: absolute;
          right: 10px;
          top: 12px;
          pointer-events: none;
        }
      }
    }
    #export-btn-group {
      float: right;
      margin-bottom: 0;
      margin-right: 0;
      margin-top: 9px;
      .export-btn, #story-btn {
        width: 30px;
        height: 30px;
        padding: 0 0;
        text-align: center;
        span {
          display: none;
        }
        i {
          margin-right: 0;
        }
        @media(min-width: 420px) {
          width: 30px;
          height: 30px;
        }
        @media(min-width: 820px) {
          padding: 0 8px;
          height: 36px;
          width: auto;
          span {
            display: inline;
          }
          i {
            margin-right: 3px;
          }
        }
      }
      #create-btn {
        height: 36px;
        line-height: 34px;
      }
      .visualisation-editor.no-template & {
        display: none;
      }
      #story-btn-form {
        display: inline-block;
      }
      #story-btn {
        border: 1px solid #ddd;
        background-color: #eee;
        color: #333;
        font-family: "Source Sans Pro";
        margin-right: 8px;
        overflow: visible;
      }
      @media(min-width: 420px) {
        margin-top: 15px;
      }

      @media(min-width: 820px) {
        margin-top: 12px;
      }
    }
  }
  .row.editor-bar {
    background: #E3E3E3;
    height: 30px;
    font-size: 12px;
    padding: 0 10px;

    #visualisation-tabs {
      z-index: 110;
      width: calc(100% - 120px);
      margin-left: 60px;
      text-align: center;
      button {
        font-size: 12px;
        line-height: 1em;
        i {
          margin-right: 3px;
          font-size: 11px;
        }
      }
    }

    #visibility-status, .read-only {
      display: inline-block;
      float: left;
      font-weight: bold;
      color: #aaa;
      margin-top: 7px;
    }

    .confirm-saved {
      float: right;
      margin-top: 8px;
      margin-left: 0;
      width: 60px;
    }
    @media(min-width: 820px) {
      #visualisation-tabs {
        width: calc(100% - 380px);
        margin-left: 0;
      }
    }
  }

  .tab-data {
    .tab-preview {
      position: fixed;
      z-index: 2;
    }
    #preview-menu {
      opacity: 0;
      pointer-events: none;
    }
  }

  .side-panel {
    text-align: left;
    position: relative;
    background: #f9f9f9;
    line-height: 1.1;
    margin-left: 0;
    padding: 10px 0 1px 0;
    width: 100%;
    &.closed {
      margin-left: -300px;
    }
    .side-panel-inner {
      margin-bottom: 87px;
      background: #fff;
      @media(min-width: 820px) {
        border: none;
        background: transparent;
      }
    }
    .side-panel-close {
      position: absolute;
      top: 15px;
      right: 10px;
      font-size: 1.25em;
      width: 1.5em;
      height: 1.5em;
      padding: 0.1em;
      text-align: center;
      border: solid 1px rgba(0, 0, 0, 0.4);
      border-radius: 3px;
      background: #eaeaea;
      transition: right 0.5s ease;
      z-index: 1; /* To put it above the adjacent iframe container*/
      &.opener {
        right: calc(-1.5em + 1px);
        border-bottom-left-radius: 0;
        border-top-left-radius: 0;
      }
      &:hover {
        opacity: 1;
        i {
          opacity: 0.7;
        }
      }
    }
    &#slides-panel {
      height: 100%;
      padding: 0;
      background: #fff;
      margin-top: 20px;
      .side-panel-scrollbox {
        padding: 0 15px 10px;
      }
      @media(min-width: 820px) {
        max-width: 200px;
        right: auto;
        position: absolute;
        top: 0;
        margin-top: 0;
        border-right: 1px solid #ddd;
      }
    }
    .side-panel-scrollbox {
      width: calc(100%);
      height: calc(100%);
      padding: 10px 10px 20px;
      overflow-y: scroll;
      .side-panel-inner {
        width: 100%;
      }
      @media(min-width: 1220px) {
        padding: 20px;
      }
    }
    h1 {
      margin: 1em 0 0.5em;
      font-size: 0.9em;
      opacity: 0.5;
      cursor: default;
    }
    h2 {
      font-size: 13px;
      letter-spacing: 0;
      font-weight: 500;
      margin: 0 0 0.6em;
      position: relative;
      color: #333;
      word-wrap: break-word;
    }
    .toplevel-settings-block {
      padding: 0 5px 20px;
      .settings-option {
        display: inline-block;
        &:first-child {
          margin-top: 9px;
        }
      }
    }
    .settings-divider {
      margin: 1rem -10px 0;
      clear: both;
      width: calc(100% + 20px);
      height: 1px;
      background: #eee;
    }
    .settings-subhead {
      text-transform: uppercase;
      font-size: 0.7rem;
      font-weight: bold;
      color: #999;
      padding: 0.5rem 5px 0.25rem;
      margin: 0;
      width: 100%;
    }
    .settings-block {
      margin: 0;
      display: inline-block;
      width: 100%;
      background-color: #fff;
      padding: 0 10px 0;
      min-height: 24px;
      h2 {
        margin: 0 -10px;
        font-size: 0.8rem;
        padding: .75em 30px;
        font-weight: bold;
        background-color: #f5f5f5;
        border-top: 1px solid #ddd;
        cursor: pointer;
        color: #535e65;
        &:focus {
          outline: none;
        }
        &:hover, &:focus {
          background: #e8e8e8;
        }
        &::after {
          content: "";
          width: 0.6rem;
          height: 0.8rem;
          position: absolute;
          left: 15px;
          background-repeat: no-repeat;
          background-position: center;
          background-image: url(data:image/svg+xml;base64,);
        }
      }
      .settings-option, .settings-divider, .settings-subhead {
        display: none;
      }

      input::-webkit-contacts-auto-fill-button {
        visibility: hidden;
        display: none !important;
        pointer-events: none;
        position: absolute;
        right: 0;
      }

      &.open {
        padding-bottom: 1rem;
        h2 {
          &::after {
            transform: rotate(90deg);
          }
        }
        .settings-option {
          display: inline-block;
          &.hidden {
            display: none;
          }
          &.option-type-color.width-quarter {
            .color-picker {
              vertical-align: top;
              line-height: 0;
              position: relative;
              &:hover:not(.is-null) {
                .cancel-setting {
                  transform: translateX(27px);
                }
              }
              &.is-null {
                .color-wrapper input {
                  opacity: 0;
                }
              }
              .cancel-setting {
                width: 30px;
                border: 1px solid #ddd;
                background: #ffffff;
                position: absolute;
                padding: 0;
                height: 30px;
                vertical-align: top;
                left: 0;
                transition: 200ms transform;
                svg {
                  margin-left: 2px;
                  width: 8px;
                  height: 8px;
                  pointer-events: none;
                  transition: 200ms opacity;
                }
                &:focus {
                  border: 1px solid #ddd;
                }
                &:hover {
                  svg {
                    opacity: 0.4;
                  }
                }
              }
            }
          }
        }
        .settings-divider, .settings-subhead {
          display: block;
        }
        h2 + .settings-divider {
          background: transparent;
          margin-top: .25rem;
        }
      }
      &.hidden {
        display: none;
      }
    }

    h3 {
      font-size: 0.8rem;
      line-height: 1.2em;
      font-weight: normal;
      margin-top: 0;
      margin-bottom: 3px;
      color: #333;
      display: inline-block;
      cursor: default;
      overflow: hidden;
      vertical-align: top;
      background: #fff;
      text-overflow: ellipsis;
    }
    p {
      font-size: 12px;
      margin-top: 0;
      font-weight: 300;
    }

    .settings-option {
      position: relative;
      margin-top: 0.75rem;
      display: none;
      vertical-align: bottom;
      width: 100%;
      padding: 0 5px;

      label {
        font-size: 13px;
        display: inline-block;
        vertical-align: top;
        width: 100%;
        .help-tip {
          margin-left: 3px;
          color: #d0d0d0;
          font-style: normal;
          &:hover {
            color: #aaa;
          }
        }

        &:hover {
          z-index: 2;
          position: relative;
        }
      }
      input, textarea, select, button {
        border-radius: 3px;
        border: 1px solid #ddd;
        padding: 0.2em 0.1em 0.2em 0.3em;
        min-height: 30px;
        font-size: 13px;
        display: block;
        outline: none;
        transition: 200ms linear border;
        max-width: 100%;
        &:focus {
          border: 1px solid #777;
        }
      }
      ::-webkit-input-placeholder {
        color: #ddd;
      }
      :-ms-input-placeholder {
        color: #ddd;
      }
      ::-ms-input-placeholder {
        color: #ddd;
      }
      .autocomplete {
        .dropdown-list {
          .dropdown-item {
            font-size: 13px;
            padding: 10px 10px 10px 6px;
          }
        }
      }
      .color-wrapper {
        width: 30px;
        height: 30px;
        overflow: hidden;
        border-radius: 3px;
        position: relative;
        display: inline-block;
        background-color: #fff;
        input {
          transform: scale(10);
          position: absolute;
        }
        &:before {
          display: block;
          position: absolute;
          content: "";
          top: 0;
          bottom: -10px;
          left: 0;
          width: 1px;
          pointer-events: none;
          background: rgba(0, 0, 0, 0.1);
          transform-origin: top;
          transform: rotate(-45deg);
        }
        &:after {
          display: block;
          position: absolute;
          content: '';
          border: 1px solid rgba(0, 0, 0, 0.1);
          left: 0;
          right: 0;
          top: 0;
          bottom: 0;
          pointer-events: none;
        }
        &:invalid {
          box-shadow: none;
        }
      }
      .single-button {
        background: #eee;
        color: #333;
        width: 100%;
        border: none;
        padding: 0;
        cursor: pointer;
        transition: 200ms linear background-color;
        &:hover {
          background-color: #e0e0e0;
        }
        &:focus {
          border: none;
        }
      }
      .buttons-container {
        display: flex;

        input[type="radio"] {
          width: 0;
          height: 0;
          opacity: 0;
          margin: 0;
          display: inline-block;
          display: none;
        }
        input[type="radio"] + label {
          font-size: 13px;
          background: #eee;
          color: #333;
          display: inline-block;
          height: 30px;
          cursor: pointer;
          text-align: center;
          padding-top: 8px;
          border-right: 1px solid #fff;
          border-bottom: 1px solid #fff;
          background-size: cover;
          background-position: 50% 50%;
          transition: 200ms linear background-color;
          i {
            color: #555;
          }
          img {
            margin-top: -8px;
          }
        }
        &.large input[type="radio"] + label {
          height: 60px;
        }
        input[type="radio"]:not([disabled]) + label:hover {
          background-color: #e0e0e0;
        }
        input[type="radio"]:disabled + label {
          cursor: default;
        }
        input[type="radio"]:checked + label {
          background-color: #ccdee6;
          cursor: default;
          position: relative;
          &:hover {
            background-color: #ccdee6;
          }
          &:after {
            width: 100%;
            height: 2px;
            background: #2886b2;
            bottom: 0;
            position: absolute;
            content: '';
            left: 0;
          }
        }
      }
      input[data-autocomplete] + .fa {
        position: absolute;
        right: 10px;
        margin-top: -21px;
        font-size: 12px;
        pointer-events: none;
      }
      select {
        width: 100%;
        height: 30px;
      }
      &.option-type-color {
        input {
          padding: 0;
          background: white;
        }
      }
      &.option-type-number,
      &.option-type-rows,
      &.option-type-color {
        width: 50%;
        &.width-quarter {
          input {
            width: 100%;
          }
        }
        input {
          width: calc((100% - 10px) / 2);
          min-width: 40px;
        }
      }
      &.option-type-boolean:not(.settings-buttons) {
        &.settings-buttons > label {
          height: auto;
        }
        input {
          display: none;
        }
        & > label:first-child {
          position: absolute;
          padding-left: 37px;
          padding-top: 7px;
          width: auto;
          right: 5px;
          left: 5px;
        }
        .slider {
          position: relative;
          cursor: pointer;
          width: 33px;
          height: 30px;
          background: transparent;
          padding-left: 0;
          padding-top: 0;
          &:hover {
            &:after {
              border-color: #bbb;
            }
          }
        }
        .slider:before {
          position: absolute;
          content: "";
          height: 12px;
          width: 33px;
          top: 9px;
          left: 0;
          border-radius: 6px;
          background-color: #ddd;
          -webkit-transition: 100ms;
          transition: 100ms;
        }
        .slider:after {
          position: absolute;
          content: "";
          height: 24px;
          width: 24px;
          left: 0;
          bottom: 3px;
          border-radius: 50%;
          background-color: #fff;
          border: 1px solid #ddd;
          box-sizing: border-box;
          -webkit-transition: 100ms;
          transition: 100ms;
        }
        input:checked + .slider {
          &:before {
            background: #ccdee6;
          }
          &:after {
            left: 9px;
            background: #2886b2;
            border-color: #2886b2;
          }
          &:hover {
            &:after {
              background-color: #2886b2;
              border-color: #2886b2;
            }
          }
        }
      }
      &.option-type-string,
      &.option-type-url {
        input {
          width: 100%;
        }
      }
      &.option-type-text,
      &.option-type-code {
        textarea {
          width: 100%;
          &.size-large {
            height: calc(50vh);
          }
        }
      }
      &.option-type-code {
        label {
          width: calc(100% - 18px);
        }
        .wrap-control {
          font-size: 0.7em;
          color: #999;
          transform: scale(1, -1);
          margin-bottom: 0.3em;
          display: inline-block;
          vertical-align: bottom;
          &.selected {
            transform: scale(-1, -1);
            cursor: pointer;
          }
          transition: transform 0.1s ease;
        }
        textarea {
          font-family: monospace;
          font-size: 13px;
        }
      }
      &.option-type-font {
        input.font-menu {
          width: 100%;
          outline: none;
        }
      }
      &.option-type-colors {
        input {
          width: 100%;
          margin: 0;
          z-index: 1;
          cursor: pointer;
        }
        .color-swatches,
        .dropdown-item {
          display: flex;
          align-items: stretch;
          min-height: 30px;
          overflow: hidden;
          em {
            font-style: normal;
            color: #777;
            display: block;
            padding: 8px;
            pointer-events: none;
          }
          span {
            display: inline-block;
            height: 30px;
            flex: 1;
            &:nth-child(n+13):nth-child(-n+99) {
              display: none;
            }
          }
        }
        .color-swatches {
          padding: 0;
          position: absolute;
          left: 4px;
          right: 26px;
          z-index: 0;
          pointer-events: none;
        }
        .dropdown-item {
          padding: 2px 0 !important;
          width: 100%;
          label {
            position: absolute;
            top: 6px;
            left: 8px;
            color: rgba(250, 250, 250, .9);
            pointer-events: none;
          }
        }
        .dropdown-item span {
          pointer-events: none;
        }
        &.custom {
          input {
            opacity: 0;
          }
          .color-swatches {
            align-items: flex-start;
            span {
              max-width: 28px;
              pointer-events: auto;
              transition: transform 100ms;
              transform-origin: center center;
              &:hover {
                transform: scale(1.2);
              }
              &.fa {
                font-size: 24px;
                color: #ccc;
                margin-left: 4px;
                &::before {
                  position: relative;
                  top: 2px;
                }
              }
            }
          }
        }
      }
      &.hidden {
        display: none;
      }
      &.width-full {
        width: 100% !important;
      }
      &.width-half {
        width: 50% !important;
      }
      &.width-quarter {
        width: 25% !important;
      }
      &.width-three-quarters {
        width: 75% !important;
      }
      .description-link {
        cursor: pointer;
        color: #dd4141;
      }
    }
    .detailed-settings {
      h2 {
        margin-bottom: .75rem;
        &:after {
          content: "";
        }
      }
    }
    .palette {
      max-height: calc(100vh - 210px);
      overflow-y: auto;
      padding: 1px;
      input[type=color],
      button,
      .swatch-color {
        -webkit-appearance: none;
        -moz-appearance: none;
        -ms-appearance: none;
        appearance: none;
        display: inline-block;
        width: 28px;
        height: 28px;
        padding: 0;
        margin: 1px;
        border: none;
        outline: none;
        background: transparent;
        &.fa-plus-square::before {
          font-size: 32px;
          color: #ddd;
          margin-left: 1px;
        }
        &.fa-times {
          position: absolute;
          right: 0;
          opacity: 0;
          color: #aaa;
          &:hover {
            color: #888;
          }
        }
        &::-webkit-color-swatch-wrapper {
          padding: 0;
        }
        &::-webkit-color-swatch {
          border: none;
        }
      }
      [draggable] {
        cursor: move;
        cursor: grab;
        cursor: -moz-grab;
        cursor: -webkit-grab;
        &:active {
          cursor: grabbing;
          cursor: -moz-grabbing;
          cursor: -webkit-grabbing;
        }
      }
      .swatch-handle {
        display: block;
        width: 15px;
        height: 28px;
        color: #ddd;
        position: absolute;
        left: 1px;
        top: 7px;
        text-align: center;
        font-size: 18px;
        font-weight: bold;
        line-height: 5.5px;
        pointer-events: none;
      }
      input[type=text] {
        width: 120px;
        width: calc(100% - 52px);
        background: transparent;
        padding: 5px 8px;
        border-width: 0;
        margin: 0;
      }
      p {
        position: relative;
        display: flex;
        align-items: center;
        margin: 0;
        padding: 5px 5px 5px 20px;
        border-bottom: 1px solid #eee;
        &:first-child {
          border-top: 1px solid #eee;
        }
        &:hover {
          button.fa-times {
            opacity: .5;
          }
        }
        &:hover [draggable]::before {
          opacity: 1;
        }
      }
    }
    .side-panel-top {
      border-top: none;
      border-bottom: 1px solid rgba(0, 0, 0, 0.1);
      margin-top: 0;
      padding: 17px 10px 20px;
      font-size: 1em;
      &.shadow {
        box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.1);
      }
      .side-panel-top-menu {
        .side-panel-icon-btn {
          color: #fff;
          background: #9A9A9A;
          padding: 7px 0 0 0;
          border-radius: 9999px;
          width: 30px;
          height: 30px;
          text-align: center;
          margin: 0;
        }
        .separator {
          margin: 0 0.4em;
          #story & { margin: 0 0.25em; }
          height: 1em;
          border-left: 1px solid rgba(0, 0, 0, 0.25);
          display: inline-block;
          vertical-align: middle;
        }
      }
    }
    .current-template {
      margin: 0;
      opacity: 0; /* hide if no template is selected*/
      transition: 200ms opacity;
      position: relative;
      padding: 10px 10px 0;
      min-height: 56px;
      border-bottom: 1px solid #ddd;
      h1 {
        padding-right: 76px;
        opacity: 1;
        font-size: 12px;
        font-weight: normal;
        min-height: 36px;
        margin: 0;
        padding-top: 3px;
        & > span {
          display: block;
          font-weight: bold;
          font-size: 16px;
        }
      }
      h3 {
        margin-top: 0.7em;
        i {
          margin-left: 0.2em;
          color: rgba(0, 0, 0, 0.5);
        }
      }
      .current-template-title {
        position: relative;
        .current-template-version {
          margin-left: 4px;
          font-size: 12px;
        }
      }
      .current-template-thumbnail {
        position: absolute;
        right: -10px;
        top: -10px;
        width: 76px;
        height: calc(100% + 19px);
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        &:after {
          width: 20px;
          height: 100%;
          position: absolute;
          left: 0;
          right: 0;
          background: linear-gradient(to right, rgba(255, 255, 255, 1), rgba(255, 255, 255, 0));
          content: '';
          display: block;
        }
      }
    }
    .template-theme {
      button#reset-to-theme:disabled {
        color: lightgray;
      }
    }
    @media (min-width: 620px) and (max-width: 819px) {
      .side-panel-inner {
        max-width: 500px;
        margin: 20px auto;
      }
    }
    @media(min-width: 820px) {
      max-width: size(3.5);
      position: fixed;
      right: 0;
      top: 93px;
      background: #fff;
      overflow-y: auto;
      height: calc(100vh - 93px);
      border-left: 1px solid #ddd;
      .current-template {
        padding-top: 0;
        min-height: 46px;
      }
    }
  }

  .tab-preview #visualisation .editor-core {
    overflow: visible !important;
  }
  .tab-preview.active #visualisation .editor-core {
    overflow: auto !important;
  }

  /* Full-screen mode*/
  body.full-screen {
    .row.editor {
      #visualisation,
      #story {
        position: fixed !important;
        top: 0 !important;
        left: 0 !important;
        width: 100% !important;
        height: 100% !important;
        .editor-core #preview {
          width: 100% !important;
        }
        #exit-full-screen {
          display: block;
        }
      }
    }
  }

  /* Loading indicator*/
  body.loading {
    .loading-spinner {
      opacity: 1 !important;
      animation-name: spin;
      animation-duration: 1.2s;
      animation-iteration-count: infinite;
      animation-timing-function: linear;
      transform-origin: center center;
    }
    .content {
      min-height: 100vh;
    }
    .preview-holder {
      pointer-events: none;
      background-color: #f3f3f3;
      #preview {
        background: transparent !important;
      }
    }
    .preview-holder iframe {
      opacity: .1;
    }
    .row.data, #blank-slide {
      opacity: 0;
      pointer-events: none;
    }
    @keyframes spin {
      0% { transform: rotate(0deg); }
      100% { transform: rotate(360deg); }
    }
  }

  #private-publishing {
    display: inline-block;
    padding-right: 15px;
    margin-top: 10px;
    width: 100%;
    & + input {
      width: 100%;
      padding: 5px;
      &.hidden {
        display: none;
      }
      &.error {
        border-color: #DD4141;
      }
    }
    &.upgrade-btn {
      width: auto;
      color: #999;
      &::after {
        top: 4px;
      }
    }
  }
}
